.card{
    display: flex;
    justify-content: center;
    margin: 20px;

}

.card img{
    width: 500px;
    border-radius: 15px;
    margin: 20px;
}

.card img:hover{
    transform: translateY(-10px);
    cursor: pointer;
    transition-duration: 0.5s;
}

.card section{
    background: rgba(1, 9, 28, 0.75);
    padding: 20px;
    border-radius: 20px;
    width: 500px;
    filter: drop-shadow(0px 4px rgba(0,0, 0.25, 0.2));
    margin-left: 25px;
}

.card h3{
    font-size:25px;
}